import helper from '../../common/helper.js';
export default {
	data() {
		return {
			userfoArray: '',
			Tagss:''
			
		};
	},
	onLoad() {
		this.userinfo()
		
	},
	onShow() {
		this.userinfo()
	},
	onPullDownRefresh: function() { //下拉刷新
		this.userinfo(true);
	},
	methods: {
		// 获取个人信息
		userinfo(isdown) {
			var userId = helper.getstate().userid;
			uni.request({
				url: helper.websiteUrl + '/usercenter/getuserinfo',
				method: 'GET',
				data: helper.postdata({
					id: userId,
				}),
				success: res => {
					if (res.data.code == 200) {
						this.userfoArray = res.data.data.baseinfo;
						this.Tagss = this.userfoArray.Tags
						if(this.Tagss == '' || this.Tagss == null){
							this.Tagss  ="未绑定平台，请在擅长领域绑定"
						}
					}
				},
				fail: () => {
					uni.showToast({
						icon: 'none',
						title: '网络异常,请稍后重试'
					});
				},
				complete: () => {
					if (isdown) {
						uni.stopPullDownRefresh(); //刷新停止
					}
				}
			});
		},
		opendata() { // 打开个人资料
			uni.navigateTo({
				url: 'personaldata',
			});
		},
		openaccount() { //打开我的账户
			uni.navigateTo({
				url: 'myaccount',
			});
		},
		opencollect() { //打开我的收藏
			uni.navigateTo({
				url: '../index/radarMap',
			});
		},
		openmessages() { //打开系统消息
			uni.navigateTo({
				url: 'messages',
			});
		},
		// 打开擅长领域
		openlingyu() {
			uni.navigateTo({
				url: 'Domain',
			});
		},
		openhelp() { //打开帮助中心
			uni.navigateTo({
				url: 'help',
			});
		},
		openzhanghu() { //打开账户安全
			uni.navigateTo({
				url: 'zhanghuanquan',
			});
		},
		openyilingqu() { //打开已领取页面
			uni.navigateTo({
				url: 'yilingqu',
			});
		},
		opendaishenhe() { //打开待审核页面
			uni.navigateTo({
				url: 'daishenhe',
			});
		},
		openyishibai() { //打开已失败页面
			uni.navigateTo({
				url: 'yishibai',
			});
		},
		openyiwancheng() { //打开已完成页面
			uni.navigateTo({
				url: 'yiwancheng',
			});
		},
		logout() {
			uni.showModal({
				title: '提示',
				content: '是否确认退出登录！',
				success: function(res) {
					if (res.confirm) {
						helper.logout();
						uni.showToast({
							icon: 'none',
							title: '您已退出登录'
						})
					} 
				}
			});
		}

	}
}
